export default {
	data() {
		return {
			shopcarts: []
		}
	},
	onShow() {
		this.query();
	},
	methods: {
		query() {
			this.$api.queryShopCart().then(res => {
				if (res.data.code === 2000) {
					this.shopcarts = res.data.data;
				} else {
					uni.showToast({
						title: '登陆过期，请重新登录',
						icon: 'none'
					})
				}
			})
		},
		removeShopCart(id) {
			this.$api.deleteShopCart(id).then(res => {
				if (res.data.code === 2000) {
					this.query();
				} else {
					uni.showToast({
						title: '操作失败，系统维护中...',
						icon: 'none'
					})
				}
			})
		},
		settlement() {
			let shopCartIds = this.shopcarts.map((shopCart) => {
				return shopCart.id
			});
			let prices = 0.00;
			this.shopcarts.forEach(shopCart => prices += shopCart.book.price);
			this.$api.settlementShopCart(shopCartIds, prices).then(res => {
				if (res.data.code === 5003) {
					uni.showToast({
						title: '登陆过期，请重新登录',
						icon: 'none'
					})
				} else if (res.data.code === 5004) {
					uni.showToast({
						title: res.data.desc,
						icon: 'none'
					})
				} else if (res.data.code === 5001) {
					uni.showToast({
						title: '操作失败，系统维护中...',
						icon: 'none'
					})
				} else {
					debugger






















					uni.getProvider({
						service: 'payment',
						success: (pro_res) => {
							uni.requestPayment({
								provider: pro_res.provider,
								appId: res.data.data.appId,
								nonceStr: res.data.data.nonceStr,
								timeStamp: res.data.data.timeStamp,
								package: res.data.data.packageValue,
								paySign: res.data.data.paySign,
								signType: res.data.data.signType,
								success: (pay_res) => {
									this.query();
									uni.showToast({
										title: '支付成功'
									})
								}
							})
						}
					})

				}
			})
		}
	}
}
